.gpt3__features-container__feature {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-self: space-between;
  align-items: flex-start;

  margin: 1rem;
}

.gpt3__features-container__feature-title {
  flex: 1;
  max-width: 180px;
  margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.04em;
  color: #ffffff;
  margin-bottom: 1rem;
}
.gpt3__features-container__feature-title div {
  width: 38px;
  height: 3px;

  background: var(--gradient-bar);
  box-sizing: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.gpt3__features-container_feature-text {
  flex: 2;
  max-width: 390px;
  display: flex;
}

.gpt3__features-container_feature-text p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #81afdd;
}

@media screen and (max-width: 600px) {
  .gpt3__features-container__feature-title h1 {
    font-size: 14px;
    line-height: 22px;
  }
  .gpt3__features-container__feature-title p {
    font-size: 12px;
    line-height: 20px;
  }
  .gpt3__features-container__feature {
    margin: 1rem 0;
  }
  .section__margin {
    margin: 1rem 2rem;
  }
}
